body{
    background-color: #fbbd1c;
}


.header{
    background-color: #d72045;
    box-shadow: 1px 1px 4px 0 rgba(0,0,0,.1);
    position:fixed;
    width: 100%;
    height: 100px;
    z-index: 3;
}

.headerImg{
    margin-left: 50px;
    margin-top: 10px;
}

.headerImg2{
    height: 40px;
}

.header ul{
    font-family: Bungee;
    font-size: 20px;
    margin: 0;
    padding: 0;
    list-style: none;
    overflow: hidden;
    background-color: #d72045;
}

.navItem{
    margin-right: 20px;
    color: white;
    display: block;
    padding: 20px;
    text-decoration: none;
    height: 100px;
    width: auto;
    line-height: 50px;
    /* border-right: 1px solid white; */
}

.navItem:hover{
    background-color: #fbbd1c;
    cursor: pointer;
}


.header .menu{
    clear: both;
    max-height: 0;
    transition: max-height .2s ease-out;
}

.header .menu-icon{
    padding: 50px 40px;
    position: relative;
    float: right;

}

.header .menu-icon .nav-icon{
    background: white;
    display: block;
    height: 4px;
    width: 30px;
    position: relative;
    transition: background .2s ease-out;
    cursor: pointer;
}

.header .menu-icon .nav-icon:before{
    background: white;
    content: "";
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    transition: all .2s ease-out;
    top: 10px;
}

.header .menu-icon .nav-icon:after{
    background: white;
    content: "";
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    transition: all .2s ease-out;
    top: -10px;
}

.header .menu-button{
    display: none;
}

.header .menu-button:checked ~ .menu{
    max-height: 500px;
}

.header .menu-button:checked ~ .menu-icon .nav-icon{
    background: transparent;
}

.header .menu-button:checked ~ .menu-icon .nav-icon:before{
    transform: rotate(-45deg);
    top:0;
}

.header .menu-button:checked ~ .menu-icon .nav-icon:after{
    transform: rotate(45deg);
    top:0;
}



