@media (min-width:60em){
    .header li{
        float: left;
    }
    .header li .navItem{
        position: 20px 30px;
    }
    .header .menu{
        clear: none;
        float: right;
        max-height: none;
    }

    .header .menu-icon{
        display: none;
    }

    
}

